<template>
<el-dialog class="add-exchange-popup" title="新增" :visible="showDialog" @close="onClose()" width="800">
    <div v-if="step == 1" class="select-type">
        <div class="type" @click="typeSelect('same')">
            同類型交換
        </div>
        <div class="type" @click="typeSelect('def')">
            不同類型交換
        </div>
    </div>
   <div v-if="step == 2" class="same">
       <el-form :model="form" label-position="left" label-width="100px">
        <el-form-item label="組合">
            <el-select v-model="form.groupName" placeholder="请選擇組合">
                <el-option v-for="(member,i) in groups" :label="member"  :value="member" :key="i"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="物品類型">
            <el-radio-group v-model="form.category">
                <el-radio v-for="(category,i) in categories" :label="category"  :value="category" :key="i"></el-radio>
            </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.category === '專卡'">
            <el-select v-model="form.album" placeholder="请選擇專輯">
                <el-option v-for="(album,i) in albums" :label="album"  :value="album" :key="i"></el-option>
            </el-select>
        </el-form-item>
        <el-upload
            action="https://jsonplaceholder.typicode.com/posts/"
            list-type="picture-card">
        <i class="el-icon-plus"></i>
        </el-upload>
        <el-form-item v-if="form.desc">
            <el-input v-model="form.name" placeholder="請詳述物品"></el-input>
        </el-form-item>
        <el-form-item label="擁有成員">
            <el-select v-model="form.ownMember" placeholder="请選擇成員">
                <el-option v-for="(member) in members" :label="member.ch"  :value="member.eng" :key="member.eng"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="欲交換成員">
            <el-checkbox-group v-model="form.targetMember">
                <el-checkbox v-for="(member) in members" :label="member.ch"  :key="member.eng"  name="type"></el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="交易方式">
            <el-checkbox-group v-model="form.exchangeWay">
                <el-checkbox v-for="(way,i) in ways" :label="way"  :key="i"  name="way"></el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        </el-form>
    </div> 
    <div class="def" v-else>

    </div>
    <div class="check">

    </div>
        
    
    <div slot="footer" class="dialog-footer">
        <el-button @click="onClose()">取 消</el-button>
        <el-button type="primary" @click="onSubmit()">确 定</el-button>
    </div>
    </el-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { addItem,getItems  } from '../../../api/exchange'; 

export default {
    props: {
        showDialog: {
            type: Boolean,
            require: true
        }
    },
    data(){
        return {
            step: 1,
            exchangeType: '',
            form: {
                groupName: '',
                album: '',
                category: '',
                itemType:'',
                exchangeWay: [],
                ownMember:'',
                targetMember: [],
            },
            members:[],
            albums:[],
            categories:['專卡','玩偶/娃娃','官方周邊','飯制周邊'],
            ways: ['店到店','賣貨便','面交','郵寄','其他'],
        }
    },
    mounted(){
        
    },
    computed:{
        ...mapState({
            groups: state => state.basic.groups,
            nctMembers: state => state.basic.nctMembers,
            nct127Members: state => state.basic.nct127Members,
            nctdreamMembers: state => state.basic.nctdreamMembers,
            wayvMembers: state => state.basic.wayvMembers,
            nctAlbums: state => state.basic.nctAlbums,
            nct127Albums: state => state.basic.nct127Albums,
            nctdreamAlbums: state => state.basic.nctdreamAlbums,
            wayvAlbums: state => state.basic.wayvAlbums,
        }),
    },
    watch:{
        'form.groupName':{
            handler(newValue){
                console.log(newValue)
                switch(newValue){
                case 'NCT':
                    this.members = this.nctMembers
                    this.albums = this.nctAlbums
                    break;
                case 'NCT127':
                    this.members = this.nct127Members
                    this.albums = this.nct127Albums
                    break;
                case 'NCT Dream':
                    this.members = this.nctdreamMembers
                    this.albums = this.nctdreamAlbums
                    break;
                case 'WayV':
                    this.members = this.wayvMembers
                    this.albums = this.wayvAlbums
                    break;
            }
            }
        }
    },
    methods:{
        typeSelect(val){
            this.exchangeType = val
            this.step = 2
        },
        onClose(){
            this.$emit('update:showDialog', false)
        },
        onSubmit(){
            this.addItem()
        },
        addItem(){
            let params = this.form
             addItem(params).then(res => {
                if (res.status == 200) {
                    console.log(res.data);
                    }
                }).catch(err => {
                    console.log(err);
                });
        },
        
    }


}
</script>

<style lang="scss" scoped>
.add-exchange-popup {
}

</style> 